.nav {
    position: fixed;
    top: 0;
    display: flex;
    z-index: 1;
    padding: 20px;
    height: 20px;
    width: 100%;
    justify-content: space-between;
    transition-timing-function: ease-in;
    transition: all 0.3s;
}
.nav_black {
    background-color: #111;
}
.nav_logo {
    position: fixed;
    left: 20px;
    width: 90px;
    object-fit: contain;
}
.nav_input {
    position: fixed;
    left: 800px;
    top: 10px;
    border-color: #ffffff;
    background-color: black;
    height: 30px;
    width: 250px;
    font-size: 14px;
    appearance: none;
    text-align: unset;
    outline: none;
    color: #ffffff;
    font-weight: 700;
    box-shadow: none;
}
.p1 {
    position: fixed;
    left: 160px;
    cursor: pointer;
    font-weight: 700;
    font-size: 12px;
}
