.App {
    background-color: black;
    color: #ffffff;
}
.row {
    position: relative;
    top: -80px;
}
.row_posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 40px;
    cursor: pointer;
}
.row_posters::-webkit-scrollbar{
    display: none;
}
.row_poster {
    object-fit: contain;
    width: 100%;
    max-height: 150px;
    margin-right: 5px;
    transition: transform 500ms;
    
}
.row_poster:hover {
    transform: scale(1.5);
}
* {
    margin: 0;
}
.row_posterLarge {
    max-height: 250px;
}
.row_posterLarge:hover {
    transform: scale(1.3);
}
.row_title {
    position: relative;
    left: -450px;
}
