.banner {
    color: #ffffff;
    object-fit: contain;
    height: 450px;
}
.banner_contents {
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
    position: absolute;
    
}
.banner_title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.banner_button2 {
    cursor: pointer;
    color: #ffffff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);    
    height: 40px;
    font-size: 20px;
    position: relative;
    left: 30px;
}
.banner_button1 {
    cursor: pointer;
    color: black;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: #ffffff;    
    height: 40px;
    font-size: 20px;
    position: relative;
    left: 30px;
}
.banner_button1:hover {
    background-color: rgba(51, 51, 51, 0.7);
    transition: all 0.2s;
}
.banner_button2:hover {
    background-color: rgba(51, 51, 51, 0.7);
    transition: all 0.2s;
}
.banner_fadeBottom {
    height: 7.4rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37, 37, 37, 0.61),
        #111111
    );
    position: relative;
    top: 335px;
}